import React from "react";
import Particles from "react-particles-js";

const ParticlesStyled = () => {
    return (
        <Particles
            style={{ position: "absolute" }}
            params={{
                particles: {
                    number: {
                        value: 300,
                    },
                    line_linked: {
                        enable: false,
                    },
                    move: {
                        speed: 4,
                    },
                    size: {
                        value: 1,
                    },
                },
            }}
        />
    );
};

export default ParticlesStyled;
